.wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgba(2, 2, 2, 0.5);
    z-index: 40;

    .hide {
        display: none;
    }
    .show {
        display: flex;
    }
    .close-icon {
        position: absolute;
        top: 10px;
        right: 15px;
        font-size: 20px;
        color: white;
        cursor: pointer;
        padding: 5px;
    }
    .upload-container {
        width: 550px;
        height: 550px;

        margin: 10px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;

        background-color: white;
        opacity: 1;
    }
    .caption-zone {
        padding: 20px;
        border-bottom: 1px solid rgb(var(--ig-separator));
    }
    .account-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;

        p {
            margin-bottom: 0;
        }
        button {
            outline: none;
            border: none;
            font-weight: 600;
            background-color: unset;
            color: rgb(var(--ig-primary-button));
            cursor: pointer;
        }
        button.wait {
            user-select: none;
            color: rgb(110, 192, 247);
        }
    }
    .reels-btn {
        margin-right: 10px;
    }

    .input-wrapper {
        width: 100%;
        height: 80px;
        margin-top: 10px;

        padding-bottom: 10px;

        textarea {
            font-family: 'Montserrat', sans-serif;
            font-family: 'Source Sans Pro', sans-serif;

            width: 100%;
            max-height: 80px;

            resize: none;
            outline: none;
            padding: 5px;
            padding-right: 19px;
            margin-left: -5px;
            font-size: 16px;
            border-radius: 5px;
            border: none;
        }
    }
    textarea::placeholder {
        color: #b3b3b3;
    }
    /* width */
    textarea::-webkit-scrollbar {
        width: 10px;
    }

    /* Track */
    textarea::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    /* Handle */
    textarea::-webkit-scrollbar-thumb {
        background: #888;
    }

    /* Handle on hover */
    textarea::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

    .image-zone {
        position: relative;
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        overflow: hidden;
    }
    .plus-icon {
        display: flex;
        justify-content: center;
        align-items: center;

        position: absolute;
        top: 10px;
        right: 10px;

        cursor: pointer;
        z-index: 5;

        color: black;
        background-color: black;
        border-radius: 50%;
    }
    .img-slider {
        display: flex;

        overflow-x: scroll;
        overflow-y: hidden;
        scroll-snap-type: x mandatory;

        scroll-behavior: smooth;

        width: 100%;
        height: 100%;
    }
    .upload-wrapper {
        position: relative;
        flex: 0 0 calc(100% - 40px);
        scroll-snap-align: start;

        border-right: 1px solid black;
    }
    video {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    .btn-delete {
        position: absolute;
        top: 50%;
        transform: translateX(-50%);
        left: 50px;

        background-color: red !important;
        cursor: pointer;
    }
    .img-slider {
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;

            margin-right: 10px;

            &:last-child {
                border-right: 1px solid transparent;
                margin-right: 50px;
            }
        }
    }
    .image-zone {
        header {
            margin: 10px 0;
            font-size: 20px;
        }
        button {
            color: white;
            background-color: rgb(var(--ig-primary-button));

            font-size: 16px;
            border-radius: 5px;
            border: none;
            padding: 5px;
            cursor: pointer;
        }
    }

    .text-center {
        text-align: center;
    }
    .progress {
        position: absolute;
        top: 0;
        left: 0;

        height: 5px;

        background-color: red;
    }
}

@media screen and (max-width: 740px) {
    .wrapper {
        .upload-container {
            width: 370px;
            height: 400px;
        }
        .img-slider img {
            border-bottom-left-radius: unset;
            border-bottom-right-radius: unset;
        }
    }
}
