.wrapper {
    position: absolute;
    bottom: 110%;
    left: 0;

    width: 250px;
    height: 300px;
    max-height: 300px;

    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(114, 132, 159, 0.3);

    background-color: white;
    color: black;

    animation: showMiniMenu 0.05s ease forwards;

    z-index: 40;

    .menu-item {
        padding: 5px 20px;
        border-bottom: 1px solid rgb(var(--ig-separator));
        font-weight: 400;

        &:hover {
            background-color: rgba(114, 132, 159, 0.05);
        }
    }
    .menu-item-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;

        height: 32px;
        width: 100%;
    }
    .account-settings {
        border-top: 4px solid rgb(var(--ig-separator));
    }
    .signout {
        border: unset;
    }

    @keyframes showMiniMenu {
        from {
            height: 0;
        }
        to {
            height: 300px;
        }
    }
}
